body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  background: #f2f7f7 !important;
  text-align: center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* tag elements	 */
html {
  font-size: 1rem;
}


h1 {
  color: black;
  font-family: "Sacramento", cursive;
  font-size: 6rem;
  font-weight: black;
  margin: 50px auto 0 auto;
  color: #66bfbf;
}

h2 {
  font-family: "Montserrat", sans-serif;
  font-size: 2.5rem;
  color: #66bfbf;
  font-weight: normal;
}

h3 {
  font-family: "Roboto", sans-serif;
  color: #11999e;
}

p {
  line-height: 2;
  margin-bottom: 10px !important;
}

a {
  color: #036569;
  font-family: "Roboto", sans-serif;
  text-decoration: none;
  padding: 25px;
}

a:hover {
  color: #eaf6f6;
}

hr {
  border-style: none !important;
  border-top: dotted !important;
  border-width: 8px !important;
  margin: 50px auto 50px auto !important;
  width: 5%;
  color: #cef2f2;
}

/* class elements */

.nav-link {
  pointer-events: all;
  transition: transform 80ms ease-in;
}


.top-container {
  background-color: #eaf6f6;
  position: relative;
  padding: 50px 0;
  height: 17rem;
}

.sub-title {
  font-family: "Sacramento", cursive;
  font-size: 1.5rem;
  color: #66bfbf;
  font-weight: normal;
}

.middle-container {
  background: #f2f7f7;
  padding-top: 1px;
}

.profile {
  margin: 1.5rem auto 0 auto;
  padding: 2rem 0;
}

.discription {
  color: #40514e;
  width: 50%;
  margin: auto;
}

.skills {
  padding-top: 3rem;
}

.skill-row {
  width: 50%;
  margin: 30px auto 40px auto;
  text-align: left;
  line-height: 1.5;
  padding: 8px 12px 12 px;
}

.skill-img1 {
  width: 25%;
  float: left;
  margin-right: 30px;
}

.skill-img2 {
  width: 25%;
  float: right;
  margin-left: 30px;
}

.contact-me {
  margin: 1rem auto 0;
  line-height: 1.5;
  padding: 0 5px 40px;
}

.bttn {
  background: #66bfbf;
  color: white;
  padding: 0.5rem 1rem 0.5rem 1rem;
  margin: 2rem 0 2rem;
  border-radius: 8px;
  text-decoration: none;
  font-weight: bold;
}
.bttn:hover {
  background-color: #eaf6f6;
  color: #66bfbf;
  text-decoration: none;
  border: 2px solid #66bfbf;
}

.bottom-container {
  background: #66bfbf;
  padding: 25px 0 5px;
}

.top-cloud {
  position: absolute;
  top: 30px;
  right: 250px;
}

.bottom-cloud {
  position: absolute;
  left: 250px;
  bottom: 2px;
}

.carousel-item img {
  filter:  grayscale(50%) brightness(25%);
}

.carousel:hover .carousel-item img {
  filter: brightness(20%);
}

.ml-auto {
  margin-left: auto;
}

.mr-4 {
  margin-right: 4rem;
}

.project-carousel {
  width: 95%;
  margin: 0 auto;
  text-decoration: none;
}

.carousel-caption h2 {
  font-size: medium;
  font-weight: 500;
}

.exp-card {
  width: 95%;
}

.exp-card p {
  line-height: 1.5;
}

@media  (min-width: 1199.98px) {
  .project-carousel {
    width: 80%;
  }
  .carousel-item img {
    filter: blur(2px) grayscale(80%) brightness(30%);
  }
  .carousel-caption h2 {
    font-size: 2.5rem;
    font-weight: normal;
  }
  .exp-card {
    width: 75%;
  }
  .exp-card p {
    line-height: 2;
  }
  


 }

/* Id elements */

#super {
  text-decoration: underline;
}

#projects {
background-color: #eaf6f6;
}

